import React from 'react';
import '../css/my.css'
import { connect } from 'react-redux'
import Sign from './sign'
import ExitHint from './ExitHint'
class My extends React.Component{
  state = {
    exit:false
  }
  componentWillMount(){
    this.props.dispatch({type:'CHANGEHEADERVSL',headerval:''})
  }
  headerClick(){
    this.setState({exit:!this.state.exit})
  }
  ensure(){
    this.setState({exit:!this.state.exit})
    this.props.dispatch({type:'CURRENTUSER',currentUser:''})
    localStorage.removeItem('petpetgoid')
  }
  render(){
    return (
      <div className={this.props.auth ? "ucenter" : null} style={{width:'100%'}}>
        {
          this.props.auth ?
          <div>
            <div className="ucenter-username">{this.props.auth}<i className="iconfont">&#xe603;</i></div>
            <ul className="ucenter-content">
              <li><i className="iconfont">&#xe602;</i><span></span><i className="iconfont">&#xe603;</i></li>
              <li><i className="iconfont">&#xe613;</i><span></span><i className="iconfont">&#xe603;</i></li>
              <li><i className="iconfont">&#xe61a;</i><span></span><i className="iconfont">&#xe603;</i></li>
              <li><i className="iconfont">&#xe624;</i><span></span><i className="iconfont">&#xe603;</i></li>
              <li><i className="iconfont">&#xe649;</i><span></span></li>
            </ul>
            {this.state.exit ? <ExitHint cancel={this.headerClick.bind(this)} ensure={this.ensure.bind(this)} val=''/> : null}
            <div onClick={this.headerClick.bind(this)} className="ucenter-exit"></div>
          </div> :
          <Sign/>
        }

      </div>
    )
  }
}
const mapStateToProps = (state) => ({
  auth:state.auth,
  signBtn:state.signBtn
})
export default connect(mapStateToProps)(My)



